import { createElement, ensureElement } from '../../utils/utils';
import { Component } from '../base/Component';
class Basket extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._list = ensureElement('.basket__list', this.container);
        this._total = ensureElement('.basket__price', this.container);
        this._button = ensureElement('.button', this.container);
        this.list = [];
        if (this._button) {
            this._button.addEventListener('click', () => {
                events.emit('order:open');
            });
        }
        this.disableButton(true);
    }
    disableButton(disabled) {
        this.setDisabled(this._button, disabled);
    }
    set list(items) {
        if (items.length) {
            this._list.replaceChildren(...items);
            this.disableButton(false);
        }
        else {
            this._list.replaceChildren(createElement('p', {
                textContent: 'Ваша корзина пуста и грустит.😭 Не дайте корзине грустить - пополните её!❤️',
            }));
            this.disableButton(true);
        }
    }
    set total(total) {
        this.setText(this._total, total.toString() + ' синапсов');
    }
}
Basket.template = ensureElement('#basket');
export { Basket };
